import React, { Component } from 'react'
import { movies } from "./getmovie";
export default class
    extends Component {
    constructor() {
        super();

        this.state = {
            genreids: {
                28: 'Action', 12: 'Adventure', 16: 'Animation', 35: 'Comedy', 80: 'Crime', 99: 'Documentary', 18: 'Drama', 10751: 'Family', 14: 'Fantasy', 36: 'History',
                27: 'Horror', 10402: 'Music', 9648: 'Mystery', 10749: 'Romance', 878: 'Sci-Fi', 10770: 'TV', 53: 'Thriller', 10752: 'War', 37: 'Western'
            },

            movie: [],
            currgen: 'All Generes',
            genoption: [],
            curtxt: ''

        }
    }

    async componentDidMount() {   // write the codes which will contains loading 
        let oldData = JSON.parse(localStorage.getItem('movies') || "[]"); // collecting data from localstorage into olddata
        this.setState({
            movie: [...oldData]
        })

        let tmp = [];

        oldData.forEach((movieobj) => {                  // pushing all generes which are in local storage movies into tmp
            if (!tmp.includes(this.state.genreids[movieobj.genre_ids[0]])) {
                tmp.push(this.state.genreids[movieobj.genre_ids[0]])
            }
        });
        tmp.unshift('All Generes')      //unishit push item at the very first
        this.setState({
            genoption: [...tmp]          // putting in the state
        })
    }





    handleDelet = (idd) => {
        this.setState({
            movie: this.state.movie.filter((movieobj) => {
                return idd != movieobj.id        // on screen delet using filter
            })
        })
        let oldData = JSON.parse(localStorage.getItem('movies') || "[]");
        oldData = oldData.filter((movie) => movie.id != idd);   // deleting movie from localstorage throught filter and movie id  passed through argument
        localStorage.setItem("movies", JSON.stringify(oldData));
    }


    handleGenere = (gen) => {      // activating sorting according to the genere

        // if not clickes in all generes

        this.setState({
            currgen: gen,

        })

        console.log(gen);


    }

     popularitydesc=()=>{
        let tmp=this.state.movie
        tmp.sort((function(obja,objb){
            return objb.popularity-obja.popularity
        }))
        this.setState({
            movie:[...tmp]
        })
     }


     ratingasc=()=>{
        let tmp=this.state.movie
        tmp.sort((function(obja,objb){
            
            return objb.vote_average-obja.vote_average
        
        }))

        this.setState({
            movie:[...tmp]
        })

     }



     ratingdesc=()=>{
        let tmp=this.state.movie
        tmp.sort((function(obja,objb){
            
            return obja.vote_average-objb.vote_average
        
        }))

        this.setState({
            movie:[...tmp]
        })

     }



     popularityasc=()=>{
        let tmp=this.state.movie
        tmp.sort((function(obja,objb){
            
            return obja.popularity-objb.popularity
        
        }))

        this.setState({
            movie:[...tmp]
        })

     }

     


    render() {

        let tmparr = []
        if (this.state.curtxt == '') {
            tmparr = this.state.movie
        }
        else {
            tmparr = this.state.movie.filter((obj) => {
                let title = obj.original_title.toLowerCase();
                return title.includes(this.state.curtxt.toLowerCase())
            })
        }
        if (this.state.currgen != 'All Generes') {    // if not clickes in all generes
            tmparr = this.state.movie.filter((movieobj) => {    // filter movie into tmp, tmp is the state which is showing
                return this.state.currgen == this.state.genreids[movieobj.genre_ids[0]]
            })
        }


        return (
            <div>
                <>
                    <div className='main'>
                        <div className='row'>
                            <div className='col-3 '>
                                <ul class="list-group mt-5 sticky-top " style={{top:'110px'}}>
                                    {
                                        this.state.genoption.map((gen) => (
                                            this.state.currgen == gen ?
                                                <li class="list-group-item bg-primary fw-bold" style={{ color: 'white' }} onClick={() => this.handleGenere(gen)}>{gen}</li> :
                                                <li class="list-group-item" onClick={() => this.handleGenere(gen)}>{gen}</li>

                                        ))
                                    }


                                </ul>
                            </div>
                            <div className='col-9 row p-3'>
                                <div className='row'>
                                    <input className='input-group-text col-12 mx-2 ' type="text" placeholder='Search' value={this.state.curtxt} onChange={(e) => this.setState({
                                        curtxt: e.target.value
                                    })} />
                                </div>
                                <table class="table" style={{ color: 'white' }}>
                                    <thead>
                                        <tr>
                                            <th scope="col">Title</th>
                                            <th scope="col"></th>
                                            <th scope="col">Genere</th>
                                            <th scope="col"><i class="fa fa-caret-down" aria-hidden="true" onClick={this.popularityasc}></i>
                                                Pupularity <i class="fa fa-caret-up" aria-hidden="true" onClick={this.popularitydesc}></i></th>
                                            <th scope="col"><i class="fa fa-caret-down" aria-hidden="true" onClick={this.ratingdesc}></i>
                                                rating <i class="fa fa-caret-up" aria-hidden="true"onClick={this.ratingasc}></i></th>
                                            <th scope="col"></th>

                                        </tr>
                                    </thead>
                                    <tbody className='align-middle'>
                                        {
                                            tmparr.map((movieobj) => (

                                                <tr style={{ height: '120px' }}>
                                                    <img className='fav-movie-list mt-2' src={`https://image.tmdb.org/t/p/original${movieobj.backdrop_path}`} />

                                                    <td className='col'>{movieobj.original_title}</td>
                                                    <td className='col'>
                                                        <>
                                                            <button type="button" class="btn btn-primary m-1">{this.state.genreids[movieobj.genre_ids[0]]}</button>

                                                        </>
                                                    </td>


                                                    <td className='col'>{movieobj.popularity}</td>

                                                    <td >{movieobj.vote_average} <i class="fa fa-star" aria-hidden="true"></i></td>

                                                    <button type="button" class="btn btn-danger bg-danger mt-2" onClick={() => this.handleDelet(movieobj.id)}>Delet</button>

                                                </tr>

                                            ))
                                        }
                                    </tbody>
                                </table>

                            </div>
                        </div>
                    </div>
                </>
            </div>
        )
    }
}
