import { movies } from "./getmovie";
import axios from 'axios';


// after pastinf bootstrap css link in public/index.html
// after pastinf bootstrap css link in public/index.html
// after pastinf bootstrap css link in public/index.html

import React, { Component } from 'react'

export default class Banner extends Component {
    constructor() {
        super();
        this.state = {
            hover: 0,
            movies: [],
            index: 0
        }
    }

    async componentDidMount() {
        const res = await axios.get(`https://api.themoviedb.org/3/movie/popular?api_key=fbcaa7fe1fbf66408921f748a4974708&language=en-US&page=${this.state.currPage}`)  // sending req through link ,    await used to wait until response come, if use await the async also add before componenet did mount [IMP]
        let data = res.data  // getting results
        this.setState({
            movies: [...data.results]
        })
        // console.log(data)


    }

    render() {

        let movie = movies.results;
        let x = Math.floor(Math.random() * 5);
        let y = Math.floor(Math.random() * 10) + 5;
        let z = Math.floor(Math.random() * 15) + 10;

        // <div className="card" style={{ position: 'relative' }} onMouseEnter={() => this.setState({ hover: 1 })} onMouseLeave={() => this.setState({ hover: '' })} >
        //     <img src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`} alt={movie.title} className="card-img-top banner-img" />

        //     <div className="card-body banner-text" >
        //         <h1 className="card-title ">{movie.original_title}</h1>
        //         <h5 className="card-text ">{movie.overview}</h5>

        //     </div>
        // </div>

        return (
            <>
                {
                    movie == '' ?
                        <div className="spinner-grow" role="status">
                            <span className="visually-hidden">Loading...</span>
                        </div> :

                        <div id="carouselExampleIndicators" class="carousel slide" data-ride="carousel">
                            <ol class="carousel-indicators">
                                <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
                                <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                                <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
                            </ol>
                            <div class="carousel-inner">
                                <div class="carousel-item active ">
                                    <img class="d-block w-100 " src={`https://image.tmdb.org/t/p/original${movie[x].backdrop_path}`} alt="First slide" />
                                    <div class="carousel-caption d-none d-md-block ">
                                        <h1 className="fw-bold ">{movie[x].original_title}</h1>
                                        <h5 className="fw-bold">{movie[x].overview}</h5>
                                    </div>
                                </div>
                                <div class="carousel-item">
                                    <img class="d-block w-100" src={`https://image.tmdb.org/t/p/original${movie[y].backdrop_path}`} alt="Second slide" />
                                    <div class="carousel-caption d-none d-md-block ">
                                        <h1 className="fw-bold ">{movie[y].original_title}</h1>
                                        <p className="fw-bold">{movie[y].overview}</p>
                                    </div>
                                </div>
                                <div class="carousel-item">
                                    <img class="d-block w-100" src={`https://image.tmdb.org/t/p/original${movie[z].backdrop_path}`} alt="Third slide" />
                                    <div class="carousel-caption d-none d-md-block">
                                        <h1 className="fw-bold">{movie[z].original_title}</h1>
                                        <p className="fw-bold">{movie[z].overview}</p>
                                    </div>
                                </div>
                            </div>
                            <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                                <span class="sr-only">Previous</span>
                            </a>
                            <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                                <span class="sr-only">Next</span>
                            </a>
                        </div>


                }
            </>
        )
    }
}
